<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process283"></a>

<p><br></p>
<p><span class="rvts17">结局3. 一统神界</span></p>
<p><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1537.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">该结局为选择了继承神性。这样做会被所有的队友和</span><span class="rvts20">NPC</span><span class="rvts21">支持（除了马拉迪有些不太情愿）。接下来神界将迎来你统治的时代，一切繁荣昌盛。你会化身为新一代神的形象，继续对抗神王。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



